.plain-text {
  cursor: pointer; }

.editable-text {
  text-align: right; }
  .editable-text .textfield {
    width: 98%;
    padding: 1%;
    font-size: 14px;
    font-family: "Open Sans", Helvetica, sans-serif;
    resize: none;
    overflow-y: auto; }
  .editable-text .button-yes, .editable-text .button-no {
    margin-left: 5px;
    cursor: pointer; }

.hidden {
  display: none; }



