.App {
  width: 1200px;
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", Helvetica, sans-serif;
  font-size: 14px; }
  .App [class^="column-"] {
    float: left;
    padding: 10px; }
  .App ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden; }
    .App ul.data-rows > li:nth-child(odd) {
      background-color: lightgray; }
  .App .column-one {
    width: 480px; }
  .App .column-two {
    width: 130px; }
  .App .column-three {
    width: 130px; }
  .App .column-four {
    width: 80px; }
  .App .column-five {
    width: 130px; }
  .App .column-six {
    width: 130px; }
  .App .hidden {
    display: none; }



