.filter-panel {
  width: 1200px;
  background-color: lightyellow;
  padding: 10px 0; }
  .filter-panel h3 {
    margin: 0 10px; }
  .filter-panel input, .filter-panel select {
    width: 100%; }
  .filter-panel .filter-titles li {
    padding-bottom: 0; }
  .filter-panel .filter-fields li {
    padding-top: 5px; }
  .filter-panel .filter-buttons {
    text-align: right; }
    .filter-panel .filter-buttons button {
      margin-right: 10px;
      width: 200px; }



